.skills-text {  
    display: flex;  
    justify-content: center;
    align-items: center;
    font-size: 20px;
}

.skills-container {
    position: relative;
    display: flex;
    right: 150px;
    justify-content: start;
    align-items: center;
    height: 80%;
    width: 150%;
    overflow-x: scroll;
    overflow-y: hidden;
    margin-top: 15px;
    padding: 15px;
    user-select: none;
    border: 2px solid rgb(87, 101, 101);
    scrollbar-color: white;
    border-radius: 7px;
    background-color: transparent; 
}

::-webkit-scrollbar {
    height: 10px;
    background: rgb(57, 53, 55);

}
::-webkit-scrollbar-thumb:horizontal {
    background: rgb(187, 181, 183);
    border-radius: 25px;
}

.skills-container .skill {
    position: relative;
    height: 200px;
    min-width: 20%;
    background: rgb(9, 6, 31);
    border-radius: 20px;
    left: 12px;
    margin: 0 20px 0 0;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    background-color: transparent;
}

.skills-container .skill .content {
    height: 85%;
    width: 90%;
    border-radius: 15px;
    margin-top: 20px;
    background-size: contain;
    background-position: center;
}

.skills-container .skill h1 {
    font-size: 20px;
    text-transform: uppercase;
    margin-top: 20px;
}

